import { FC, useContext } from "react"

import { TotalScoreContext } from "../../../../state/scoreContext"
import { Answer, AnswersProps } from "../../../../types/game"
import { RectangleButtonWrapper } from "../../../RectangleButtonWrapper/RectangleButtonWrapper"

import classes from "./Answers.module.css"

export const Answers: FC<AnswersProps> = ({ answers, money }: AnswersProps) => {
  const { stage } = useContext(TotalScoreContext);

  return (
    <div className={classes.wrapper}>
      {answers.map((item: any, index: number) =>
        stage === index ? item.map((answer: Answer, i: number) =>
          <RectangleButtonWrapper key={Math.random()} letterIndex={i} money={money} text={answer.answer} isCorrect={answer.isCorrect} />) : null)}
    </div>
  )
}
