import { FC } from "react"

import { RectangleButtonProps } from "../../types/rectangleButton";

import classes from "./RectangleButton.module.css"

const chooseColor = (isHover: boolean, isClicked: boolean, isCorrect: boolean, isLine: boolean): string => {
  if (isClicked && isCorrect) return isLine ? "#47D867" : "#E6FAEA";
  if (isClicked && !isCorrect) return isLine ? "#EC6259" : "#FDEEED";
  if (isHover) return isLine ? "#FF8B37" : "#FFF3EB";
  return isLine ? "#000" : "#fff";
}

export const RectangleButton: FC<RectangleButtonProps> = ({ text, isCorrect, letter, setIsHover, handleClick, isHover, isClicked, rectangleColor, lineColor }: RectangleButtonProps) => (
  <div tabIndex={-1} role="button" className={classes.rectangleButton}
    onMouseEnter={() => setIsHover(true)}
    onMouseLeave={() => setIsHover(false)}
    onMouseDown={handleClick}>
    <svg width="25" height="2" viewBox="0 0 69 2" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path d="M69 1H0" stroke={lineColor} />
    </svg>

    <svg width="389" height="72" viewBox="0 0 240 40" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path fill={isClicked ? "#fefefe" : rectangleColor} d="M13.4526 4.63788C15.6376 2.01596 18.8742 0.5 22.2872 0.5H217.713C221.126 0.5 224.362 2.01597 226.547 4.63788L239.349 20L226.547 35.3621C224.362 37.984 221.126 39.5 217.713 39.5H22.2872C18.8742 39.5 15.6376 37.984 13.4526 35.3621L0.650853 20L13.4526 4.63788Z" stroke={chooseColor(isHover, isClicked, isCorrect, true)} />
    </svg>
    <p className={classes.answerText}><span className={classes.answerLetter}>{letter}</span>{text}</p>

    <svg width="25" height="2" viewBox="0 0 69 1" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path d="M69 1H0" stroke={lineColor} />
    </svg>
  </div>
)

