import { FC, useState, useContext } from "react"
import { addLetter } from "../../helpers/ addLetter";
import { TotalScoreContext } from "../../state/scoreContext";
import { RectangleButtonWrapperProps } from "../../types/rectangleButton"
import { RectangleButton } from "../RectangleButton/RectangleButton";

const chooseColor = (isHover: boolean, isClicked: boolean, isCorrect: boolean, isLine: boolean): string => {
  if (isClicked && isCorrect) return isLine ? "#47D867" : "#E6FAEA";
  if (isClicked && !isCorrect) return isLine ? "#EC6259" : "#FDEEED";
  if (isHover) return isLine ? "#FF8B37" : "#FFF3EB";
  return isLine ? "#000" : "#fff";
}

export const RectangleButtonWrapper: FC<RectangleButtonWrapperProps> = ({ text, isCorrect, money, letterIndex }: RectangleButtonWrapperProps) => {
  const [isHover, setIsHover] = useState(false);
  const [isClicked, setIsClicked] = useState(false);
  const { setCurrentScore, setCurrentStage, stage } = useContext(TotalScoreContext);

  const handleClick = () => {

    setIsClicked(true);
    if (isCorrect) {
      money.forEach((item, index) => {
        if (index === stage) {
          setCurrentScore(item.money);
        }
      });
    } else {
      setCurrentStage(12)
    }

    setTimeout(() => setCurrentStage(stage + 1), 500);
  }

  const letter = addLetter(letterIndex);
  const lineColor = chooseColor(isHover, isClicked, isCorrect, true);
  const rectangleColor = chooseColor(isHover, isClicked, isCorrect, false)

  return <RectangleButton lineColor={lineColor}
    rectangleColor={rectangleColor}
    letter={letter} text={text}
    isCorrect={isCorrect} isHover={isHover}
    isClicked={isClicked} setIsHover={setIsHover}
    handleClick={handleClick} />
}
